<template>
 <v-container>
   <h1 class="mt-16">CONTACT US</h1>
   <v-card flat>
    <v-card-title></v-card-title>
    <v-col>
      <v-row justify="space-around">
      
        <v-card class="text-center" >
          <v-card-title>DIGI SHOPEE ADV PRIVATE LIMITED <br></v-card-title>
          <h4><v-icon>mdi-map-marker</v-icon>Corporate Office</h4>
          <h5>#172/3, 1ST Floor <br> Kathriguppe Main Road<br> Vivekananda Nagar <br> Banashankari, 3rd Stage<br>Bangalore 560085</h5>
        </v-card>
        <v-card flat>
          <v-card  flat class="flot-right">
         <div class="googlemap">
           <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3888.6375602101975!2d77.55195951430403!3d12.93100071926778!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bae3f0399d0bfcb%3A0xbd8b69b15eda2df1!2sGoshopee%2C%20Digi%20E%20Shopee%20Adv%20Pvt%20Ltd%2C!5e0!3m2!1sen!2sin!4v1606989231909!5m2!1sen!2sin" width="460" height="280" frameborder="20" style="border:30;" allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>
         </div>
          </v-card>
        </v-card>
      </v-row>
    </v-col>
  </v-card>
  <v-card class="mt-16" flat>
           <v-form
           @submit.prevent="sendform"
          ref="form"
          v-model="valid"
          lazy-validation
          enctype="multipart/formdata"
         >
         <v-text-field
          v-model="name"
          :rules="nameRules"
          label="Name"
          required
          @change="selectfile"
         >
         </v-text-field>
         <v-text-field
         v-model="email"
         :rules="emailRules"
         label="E-mail"
         required
         @change="selectfile"
         >
         </v-text-field>
         <v-text-field
          v-model="Message"
          :rules="messageRules"
          label="Message"
          required
          @change="selectfile"
         >
         </v-text-field>
         <v-btn
          :disabled="!valid"
          color="success"
          class="mr-4"
          @click="validate"
         >
          Submit
         </v-btn>
         <v-btn
          color="error"
          class="mr-4"
          @click="reset"
         >
          Reset 
         </v-btn>
         </v-form>
         </v-card>
 </v-container>
</template>
  <script>
  export default {
    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required',
      
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      select: null,
     
    }),

    methods: {
      validate () {
        this.$refs.form.validate()
      },
      reset () {
        this.$refs.form.reset()
      },
      resetValidation () {
        this.$refs.form.resetValidation()
      },
    },
  }
</script>
